* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Quicksand", sans-serif;
    list-style: none;
    text-decoration: none;
    scroll-behavior: smooth;
  }
  
  :root {
    --bg-color: #e9e9e9;
    --text-color: black;
    --main-color: #4FC3F7;
    --second-color: gray;
    --other-color: #53db81;
    --h1-font: 3.7rem;
    --h2-font: 2.7rem;
    --p-font: 1.1rem;
  }

  html, body {
    margin: 0;
    display: flex;
    flex-direction: column;
    background: var(--bg-color);
    color: var(--text-color);
    height: 100vh;
  }

  main {
    flex: 1;
  }
  
h2 {
  font-size: var(--h2-font);
  margin-top: 15px;
}

.main-text{
  text-align: center;
}

section {
  padding: 80px 16% 90px;
}

